#landing .legend {
    font-family: 'Raleway', sans-serif;
    font-weight:bold;
    color:rgb(255,255,252);
    text-shadow: 3px 3px rgba(0,0,0,0.9);
    font-size:4rem;
    line-height: 95px;
    margin-top:35%;
    margin-left:20%;
    
}

@media (min-width:1000px){
    #landing .legend {
        font-size:4.5rem;
        margin:0 auto;
        margin-top:65%;
    }

    #landing div div {
        left:50%;
        top:50%;
        -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%),;
            transform: translate(-50%, -50%),
    }
}



