.eventCard {
  height: 200px;
  width: 100%;
  -webkit-box-shadow: 2px 2px 2px #888888;
          box-shadow: 2px 2px 2px #888888;
}


.cardLeft {
  position: relative;
  width: 30%;
  height: 100%;
  display: inline-block
}

.cardLeft img {
  height:100%;
  width:100%
}

.cardLeftTopDetails {
  position: absolute;
  top: 0px;
  right: 0px;
  padding: 0px 5px;
  margin: 5px;
  border-radius: 5px;
  text-align:right;
  background-color: #4f4f4f;
  color: white
}

.cardLeftTopDetails p {
  margin: auto;
  font-size: 20px;
}

.cardLeftBottomDetails {
  position: absolute;
  bottom: 0px;
  width:100%;
  text-align:right;
  background-color:#024b71;
  color: white
}

.cardLeftBottomDetails h5 {
  margin: 10px
}

.cardRight {
  width: 70%;
  height: 100%;
  padding-left: 10px;
  display: inline-block;
  position: relative;
}

.cardRight .date {
  color: grey;
  text-align: left;
}

.cardRight .location {
  position: absolute;
  bottom: 0px;
}



